import React from "react";
import { StaticImage } from "gatsby-plugin-image";
import "bootstrap/dist/css/bootstrap.min.css";
import Carousel from "react-bootstrap/Carousel";

const slides = [
  "Czyszczenie komputera",
  "Wymiana podzespow",
  "Doradzenie w wyborze sprztu",
];

const CarouselSection = () => {
  return (
    <Carousel id="home">
      <Carousel.Item>
        <StaticImage src="../images/pexels1.jpg" alt={slides[0]} />
      </Carousel.Item>
      <Carousel.Item>
        <StaticImage src="../images/pexels2.jpg" alt={slides[1]} />
      </Carousel.Item>
      <Carousel.Item>
        <StaticImage src="../images/pexels3.jpg" alt={slides[2]} />
      </Carousel.Item>
    </Carousel>
  );
};

export default CarouselSection;
